// Funcin para mapear una lista de movimientos de la API a un modelo visual:

export const mapMovementsListApiToVisualModel = movementsList => {

// Utilizamos el mtodo `map` para aplicar la funcin `mapMovementsApiToVisualModel` a cada movimiento
// en la lista de movimientos y crear un nuevo array con los resultados mapeados.

    return movementsList.map(movement => mapMovementsApiToVisualModel(movement));
};

// Funcin para mapear un movimiento de la API a un modelo visual.

const mapMovementsApiToVisualModel = movement => {

    // Creamos y devolvemos un objeto con campos mapeados desde el objeto 'movement' de la API
    // Se formatean las fechas utilizando el mtodo 'toLocaleDateString'.

    return {
        transaction: new Date(movement.transaction).toLocaleDateString(),
        realTransaction: new Date(movement.realTransaction).toLocaleDateString(),
        description: movement.description,
        amount: `${movement.amount} `, // Se realiza la interpolacin y agregamos el smbolo de euro al importe.
        balance: `${movement.balance} `, // Se realiza la interpolacin y agregamos el smbolo de euro al saldo.
    };
};

// Funcin para mapear una cuenta de la API a un modelo visual:

export const mapAccountApiToVisualModel = account => {
// Creamos y devolvemos un objeto con campos mapeados desde el objeto 'account' de la API.
// Se hace la interpolacin de la propiedad balance y utilizamos el smbolo de euro para el campo de saldo.
    return {
        balance: `${account.balance} `,
        iban: account.iban,
        alias: account.name,
    };
};
